#home {
  position: relative;
  background: url(../../assets/bgIMG.png);
  background-size: cover;
  background-repeat: repeat;
  background-position: center;
}

.app__wrapper {
  padding: 0;
  .copyright {
    display: none;
  }
}

.app__header {
  flex: 1;
  height: 100%;
  width: 100%;
  flex-direction: row;
  padding: 6rem 2rem 0;

  @media screen and (min-width: 2000px) {
    padding-top: 8rem;
  }

  @media screen and (max-width: 1200px) {
    flex-direction: column;
    align-items: center;
  }

  @media screen and (max-width: 450px) {
    padding: 6rem 1rem 2rem;
  }
}

.app__header-info {
  flex: 0.65;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;
  margin-right: 2rem;
  padding-bottom: 1rem;

  @media screen and (max-width: 2000px) {
    width: 100%;
  }

  @media screen and (max-width: 1200px) {
    justify-content: center;
    align-items: center;
  }
}

.app__header-badge {
  width: 100%;
  display: flex;
  flex-direction: column;

  .badge-cmp,
  .tag-cmp {
    padding: 1rem 2rem;
    border: var(--white-color);
    border-radius: 15px;
    flex-direction: row;
    width: auto;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  }

  .tag-cmp {
    flex-direction: column;
    margin-top: 3rem;

    p {
      width: 100%;
      text-transform: uppercase;
      text-align: center;
      margin: 0.25rem 0;
      font-weight: 500;
    }
  }

  span {
    font-size: 2.5rem;
    @media screen and (min-width: 2000px) {
      font-size: 5rem;
    }
  }

  @media screen and (max-width: 1200px) {
    justify-content: flex-start;
    align-items: flex-start;
  }
}

.app__header-circles {
  flex: 0.75;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  height: 100%;
  margin-left: 2rem;

  div {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background: var(--white-color);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    margin: 1rem;
    img {
      width: 60%;
      height: 60%;
    }
  }

  div:nth-child(1) {
    width: 150px;
    height: 150px;
  }
  div:nth-child(2) {
    width: 120px;
    height: 120px;
    align-self: center;
  }
  div:nth-child(3) {
    width: 80px;
    height: 80px;
    align-self: flex-end;
  }
  div:nth-child(4) {
    width: 100px;
    height: 100px;
  }

  @media screen and (min-width: 2000px) {
    div:nth-child(1) {
      width: 300px;
      height: 300px;
    }
    div:nth-child(2) {
      width: 240px;
      height: 240px;
      align-self: center;
    }
    div:nth-child(3) {
      width: 160px;
      height: 160px;
      align-self: flex-end;
    }
    div:nth-child(4) {
      width: 200px;
      height: 200px;
    }
  }

  @media screen and (max-width: 1200px) {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: 0;
    div {
      margin: 1rem;
    }
  }
}

.app__header-img {
  display: flex;
  height: 100%;
  flex: 1;
  justify-content: flex-end;
  align-items: flex-end;
  position: relative;

  img {
    width: 100%;
    object-fit: contain;
    z-index: 1;
  }

  .overlay_circle {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    width: 100%;
    height: 90%;
  }
}
